import { reduceEachTrailingCommentRange } from 'typescript';
import { FORM_DISPLAY, MOVIE_DISPLAY } from './actionTypes';

const formInitialState = {
  formDisplay: 'LOGIN_FORM',
};

const usersChatInitialState = {
  movie: [],
};

export const formReducer = (state = formInitialState, action: any) => {
  switch (action.type) {
    case FORM_DISPLAY:
      return { ...state, formDisplay: action.payload };
    default:
      return state;
  }
};

export const MovieReducer = (state = usersChatInitialState, action: any) => {
  switch (action.type) {
    case MOVIE_DISPLAY:
      return { ...state, movie: action.payload };
    default:
      return state;
  }
};
