import React, { useState } from 'react';
import { Form, FormControl, Button } from 'react-bootstrap';
import {
  MDBContainer,
  MDBNavbar,
  MDBNavbarBrand,
  MDBNavbarToggler,
  MDBIcon,
  MDBNavbarNav,
  MDBNavbarItem,
  MDBNavbarLink,
  MDBCollapse,
} from 'mdb-react-ui-kit';
import { message, Space } from 'antd';
import { Link, Router, useHistory } from 'react-router-dom';
import { getMovieByName, getMoviesById } from '../services/Movie-Services';
import { useDispatch } from 'react-redux';
import '../index.css'

export default function Homepage() {
  const [showBasic, setShowBasic] = useState(false);
  const history = useHistory();
  const [search, setsearch]:any = useState();
  const dispatch = useDispatch();

  message.config({
    top: 60,
    rtl: false,
  });

  const success = () => {
    message.success('You have been successfully loggeg out');
  };

  function logout() {
    localStorage.clear();
    success();
    history.push('/');
  }
  window.onload = function () {
    const token = localStorage.getItem('login');
    if (token) history.push('/homepage');
    else history.push('/');
  };


  function handleSearch(e:any) {
    e.preventDefault();
    console.log('HandleSearch', search);
    
    if(search.startsWith("tt"))
    {
      console.log('starts with tt');
      
      getMoviesById(dispatch, search).then(() => history.push(`/search?s=${search}`)
      ).catch((err) =>  history.push('/error'))
    }else{
      console.log('starts with Name');

      getMovieByName(dispatch, search).then(() => history.push(`/search?i=${search}`)
      ).catch(() =>  history.push('/error'))
    }
  }

  return (
      <div>
        <MDBNavbar sticky={true} expand='lg' light bgColor='white'>
          <MDBContainer fluid>
            <MDBNavbarBrand className='app-name'>Movies</MDBNavbarBrand>
            <MDBNavbarToggler
              aria-controls='navbarSupportedContent'
              aria-expanded='false'
              aria-label='Toggle navigation'
              onClick={() => setShowBasic(!showBasic)}
            >
              <MDBIcon icon='bars' fas />
            </MDBNavbarToggler>

            <MDBCollapse navbar show={showBasic}>
              <MDBNavbarNav className='mr-auto mb-2 mb-lg-0'>
                <MDBNavbarItem>
                  <Link to='/homepage'>
                    <MDBNavbarLink href='#'>Home</MDBNavbarLink>
                  </Link>
                </MDBNavbarItem>

                <MDBNavbarItem>
                  <MDBNavbarLink href='#' tabIndex={-1} aria-disabled='true'>
                    <h6 style={{ margin: '4.6% 0%' }} onClick={() => logout()}>
                      Logout
                    </h6>
                  </MDBNavbarLink>
                </MDBNavbarItem>
              </MDBNavbarNav>

              <Form
                style={{
                  display: 'grid',
                  width: '40%',
                  gridTemplateColumns: '65% 35%',
                }}
                inline
              >
                <FormControl type='text' placeholder='Search' onChange={(e:any) => setsearch(e.target.value) } className='mr-sm-1' />
                <Button type='submit' onClick={(e:any) => handleSearch(e)} style={{ margin: '0 auto' }}>
                  Submit
                </Button>
              </Form>
            </MDBCollapse>
          </MDBContainer>
        </MDBNavbar>
      </div>

  );
}


