import React from 'react';
import { useHistory } from 'react-router-dom';
import logo from './2480259.jpg'

export default function AppEror() {
  const history = useHistory();
  return (
    <div >
    <img onClick={() => history.push('/')} style={{width:'63%', cursor:'pointer',  margin:'auto 17%'}} src={logo} alt="" />
    </div>
  );
}
