import React, { ChangeEvent, useContext, useEffect, useState } from 'react';
import { Card, Avatar } from 'antd';
import Rating from '@material-ui/lab/Rating';
import { StarFilled, SearchOutlined, EditOutlined, EllipsisOutlined, SettingOutlined } from '@ant-design/icons';
import { BrowserRouter as Router, Route, Switch, Link } from 'react-router-dom';
import { EditIcon, IconButton, Popover, Pane, TextInput, SearchIcon, majorScale } from 'evergreen-ui';
import { useSelector, useDispatch } from 'react-redux';
import { useHistory } from 'react-router-dom';

import axios from 'axios';
import '../index.css';
import { getMovies } from '../services/Movie-Services';
import Homepage from './Homepage';

interface Props {}
export default function Search(props: Props) {
  const dispatch = useDispatch();
  const displayMovies = useSelector((state: any) => {
    console.log(state.movie.movie);
    return state.movie.movie;
  });

  useEffect(() => {
    getMovies(dispatch);
  }, []);

  const { Meta } = Card;
  return (
    <div className='main-div container'>
      <Homepage />
      {displayMovies ? (
        displayMovies.map((val: any) => {
          return (
            <div
              style={{
                display: 'inline-block',
                width: '20%',
                maxHeight: '30%',
                padding: '15px',
                // gridTemplateColumns: '55% 45%',
              }}
              className='director-details'
            >
              <Card
                className='directorcontainer1'
                cover={<img className='container photo director-border' alt='example' src={val.Poster} />}
                actions={[
                  <Link to='/directorsearch'>
                    <SearchOutlined key='search' />
                  </Link>,
                  <IconButton className='trashicon' icon={EditIcon} intent='warning' marginRight={majorScale(0)} />,
                  <EllipsisOutlined key='ellipsis' />,
                ]}
              >
                <Meta title={val.Title} description={`Release Year- ${val.Year}`} />
                {/* <Button marginY={8} display={'inline'} marginRight={8} iconBefore={EditIcon}>
                  Edit
                </Button>
                <Button marginY={5} marginRight={8} iconBefore={SearchIcon}>
                  Search
            </Button> */}
              </Card>
            </div>
          );
        })
      ) : (
        <div />
      )}
    </div>
  );
}
