import axios from "axios";
import { MOVIE_DISPLAY } from "../redux/actionTypes";
import { message, Button, Space } from 'antd';


const error = () => {
  message.error('You have been successfully loggeg out');
};


export const getMovies = async (dispatch: any) => {
  let res = await axios.get("/api/movie");
  console.log('Services  ', res);
  
  dispatch({ type: MOVIE_DISPLAY, payload: res.data });
};


export const getMoviesById = async (dispatch: any, id: any) => {
  let res = await axios.get(
    "/api/movie/" + id
  );
  console.log('getMoviesById  ', res);

  dispatch({ type:  MOVIE_DISPLAY, payload: res.data });
};


export const getMovieByName = async (dispatch: any, movie: any) => {
  let res = await axios.get(
    "/api/movie/search/" + movie);
    console.log('getMovieByName  ', res);

  dispatch({ type:  MOVIE_DISPLAY, payload: res.data });
};